<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="item.product.img" width="120" height="120" :alt="item.product.title">
    </div>
    <h3 class="product__title">
      {{ item.product.title }}
    </h3>
    <span class="product__code">
      Артикул: {{ item.productId }}
    </span>
    
    <BaseCounter v-model="amount" />

    <b class="product__price">
      {{ (item.amount * item.product.price) | numberFormat }} ₽
    </b>
  
    <button class="product__del button-del" 
      type="button" 
      aria-label="Удалить товар из корзины"
      @click.prevent="deleteCartProduct(item.productId)" >
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import BaseCounter from '@/components/BaseCounter';

export default {
  props: ['item'],
  filters: { numberFormat },
  components: { BaseCounter},
  computed: {
    amount: {
      set(value) {
        this.$store.dispatch('updateCartProductAmount', {productId: this.item.productId, amount: value})
      },
      get() {
        return this.item.amount;
      },
    }
  },
  methods: {
    deleteCartProduct(productId) {
      this.$store.dispatch('deleteCartProduct', productId)
    }
  }

}
</script>