<template>
  <div class="form__counter">
    <button type="button" aria-label="Убрать один товар" @click="dic">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>
  
    <input type="text" v-model="productAmount" name="count" @change="change">
  
    <button type="button" aria-label="Добавить один товар" @click="inc">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<script>
export default {
  props:['productAmount'],
  model: {
    prop: 'productAmount',
    event: 'counter-change'
  },
  methods: {
    inc() {
      this.$emit('counter-change', ++this.productAmount)
    },
    dic() {
      if(this.quantity !== 0) {
        this.$emit('counter-change', --this.productAmount);
      }
    },
    change() {
      this.$emit('counter-change', this.productAmount);
    }
  },

}

</script>