<template>
  <li class="catalog__item">
    <router-link class="catalog__pic" :to="{name: 'product', params: {id: product.id}}">
      <img :src="product.img" alt="Название товара">
    </router-link >

    <h3 class="catalog__title">
      <a href="#">
        {{ product.title }}
      </a>
    </h3>

    <span class="catalog__price">
      {{ product.price | numberFormat}} ₽
    </span>

    <ul class="colors colors--black">
      <li class="colors__item" v-for="el in color">
        <label for="color-1" class="colors__label">
          <input class="colors__radio sr-only" type="radio" name="color-1" :value="el.id" :checked="colorChecked === el.id" :key="el.id">
          <span class="colors__value" :style="{'background-color': el.code}">
          </span>
        </label>
      </li>
    </ul>
  </li>
</template>

<script>
import colors from '@/data/colors';
import numberFormat from '@/helpers/numberFormat';

export default {
  props: ['product', 'colorChecked'], 
  computed: {
    color() {
      return colors.filter(color => this.product.colors.includes(color.id))
    }
  },
  filters: {
    numberFormat
  }
}
</script>
