<template>
  <div>
    <main class="content container">
      <div class="content__top content__top--catalog">
        <h1 class="content__title">
          Каталог
        </h1>
        <span class="content__info">
          152 товара
        </span>
      </div>
      <div class="content__catalog">
        <ProductFilter :price-from.sync="filteredPriceFrom" :price-to.sync="filteredPriceTo"
          :category-id.sync="filteredCategoryId" :color-id.sync="filteredColorId" />
        <section class="catalog">
          <div v-if="productsLoading">Загрузка товаров....</div>
          <div v-else-if="productsLoadingFailed">Произошла ошибка загрузки товаров <button @click.prevent="loadProducts">Перезагрузить</button></div>
          <ProductsList v-else :products="products" :checked-color="filteredColorId" />
          <BasePagination v-model="page" :count="countProducts" :per-page="productsPerPage" />
        </section>
      </div>
    </main>
  </div>

</template>

<script>
  import ProductsList from '@/components/ProductsList';
  import BasePagination from '@/components/BasePagination';
  import ProductFilter from '@/components/ProductFilter';
  import axios from 'axios';
  import { API_BASE_URL } from '@/config';

  export default {
    components: {
      ProductsList,
      BasePagination,
      ProductFilter,
    },
    data: function () {
      return {
        page: 1,
        productsPerPage: 3,
        filteredPriceFrom: 0,
        filteredPriceTo: 0,
        filteredCategoryId: 0,
        filteredColorId: 0,
        productsData: null,
        productsLoading: false,
        productsLoadingFailed: false,
      }
    },
    computed: {
      products() {
        return this.productsData ? this.productsData.items.map(product => {
          return {
              ...product,
              img: product.image.file.url
            }
          }) : [];
      },
      countProducts() {
        return this.productsData ? this.productsData.pagination.total : 0;
      },
    },
    watch: {
      page() {
        this.loadProducts();
      },
      filteredPriceTo() {
        this.loadProducts();
      },
      filteredColorId() {
        this.loadProducts();
      },
      filteredPriceFrom() {
        this.loadProducts();
      },
      filteredCategoryId() {
        this.loadProducts();
      }
    },
    methods: {
      loadProducts() {
        this.productsLoading = true;
        this.productsLoadingFailed = false;
        clearTimeout(this.loadProductTimer);
        this.loadProductTimer = setTimeout(()=> {
          axios.get(API_BASE_URL + '/api/products', {
            params: {
              page: this.page,
              limit: this.productsPerPage,
              categoryId: this.filteredCategoryId,
              minPrice: this.filteredPriceFrom,
              maxPrice: this.filteredPriceTo,
              colorId: this.filteredColorId,
            }
          })
            .then(response => this.productsData = response.data)
            .catch((error) => this.productsLoadingFailed = true)
            .finally(() => this.productsLoading = false)
        }, 0);
      }
    },
    created() {
      this.loadProducts();
    }
  };
</script>