<template>
  <ul class="catalog__pagination pagination">
    <li class="pagination__item">
      <button class="pagination__link pagination__link--arrow" :class="{'pagination__link--disabled': page === 1}" @click.prevent="prev" aria-label="Предыдущая страница" :disabled="page === 1">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-left"></use>
        </svg>
      </button>
    </li>
    <li class="pagination__item" v-for="pageNumber in pages" :key="pageNumber">
      <a href="#" 
        class="pagination__link" 
        :class="{'pagination__link--current': pageNumber === page}" 
        @click.prevent="paginate(pageNumber)"
        >
        {{ pageNumber }}
      </a>
    </li>

    <li class="pagination__item">
      <button class="pagination__link pagination__link--arrow" :class="{'pagination__link--disabled': page === pages}" @click.prevent="next" :disabled="page === pages" aria-label="Следующая страница">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-right"></use>
        </svg>
      </button>
    </li>
  </ul>
</template>

<script>

export default {
  props: ["page", "count", "perPage"],
  model: {
    prop: 'page',
    event: 'paginate',
  },
  computed: {
    pages() {
      return Math.ceil(this.count / this.perPage);
    }
  },
  methods: {
    paginate: function(page) {
      this.$emit('paginate', page)
    },
    next() {
      if(this.page < this.pages) {
        this.paginate(this.page + 1);
      } 
    },
    prev() {
      if (this.page > 1) {
        this.paginate(this.page - 1);
      }
    },

  }
}
</script>