import Vue from 'vue';
import Veux from 'vuex';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

Vue.use(Veux);

export default new Veux.Store({
  state: {
    cartProducts: [],
    userAccessKey: null,
    cartProductsData: [],
    orderInfo: null,
  },
  mutations: {
    updateOrderInfo(state, orderInfo) {
      state.orderInfo =  orderInfo
    },

    resetCart(state) {
      state.cartProducts = [];
      state.cartProductsData = [];
    },

    updateCartProductAmount(state, {productId, amount}) {
      const item = state.cartProducts.find(el => el.productId === productId);
      if(item) {
        item.amount = amount;
      }
    }, 

    deleteCartProduct(state, productId) {
      state.cartProducts = state.cartProducts.filter(item => item.productId !== productId);
    },

    updateUserAccessKey(state, userAccessKey) {
      state.userAccessKey = userAccessKey;
    },

    updateCartProductsData(state, cartProductsData) {
      state.cartProductsData = cartProductsData;
    },

    syncCartProducts(state) {
      state.cartProducts = state.cartProductsData.map(item => {
        return {
          productId: item.product.id,
          amount: item.quantity,
        }
      })
    }
  }, 
  getters: {
    cartDetailsProduct(state) {
      return state.cartProducts.map(item => {
        const product = state.cartProductsData.find(p => p.product.id === item.productId).product;
        return {
          ...item,
          product: {
            ...product,
            img: product.image.file.url
          }
        }
      })

    },
    cartTotalPrice(state, getters) {
      return getters.cartDetailsProduct.reduce((total, item) => 
        (item.product.price * item.amount) + total, 0)
    },
    quantity(state, getters) {
        return getters.cartDetailsProduct.reduce((total, item) => item.amount + total, 0)
    }
  }, 
  actions: {
    loadCart(context) {
      return axios.get(API_BASE_URL + '/api/baskets', {
        params: {
          userAccessKey: context.state.userAccessKey,
        }
      })
        .then(response => {
          if(context.state.userAccessKey === null) {
            localStorage.setItem('userAccessKey', response.data.user.accessKey);
            context.commit('updateUserAccessKey', response.data.user.accessKey);
          }
          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProducts');
        })
    },

    addProductToCart(context, {productId, amount}) {
      return axios.post(API_BASE_URL + '/api/baskets/products', {
        productId: productId,
        quantity: amount,
      }, {
            params: {
              userAccessKey: context.state.userAccessKey,
            }
          })
        .then(response => {
          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProducts');
        })
    },

    deleteCartProduct(context, productId) {
      return axios.delete(API_BASE_URL + `/api/baskets/products?userAccessKey=${context.state.userAccessKey}`, {
              data: {
                "productId": productId.toString(),
              }})
        .then(response => {
          context.commit('deleteCartProduct', productId);
          context.commit('syncartproducts');
        })
    },
    
    updateCartProductAmount(context, {productId, amount}) {
      context.commit('updateCartProductAmount', {productId, amount});
      if(amount < 1) {
        return;
      }

      return axios.put(API_BASE_URL + '/api/baskets/products', {
        productId: productId,
        quantity: amount,
      }, {
        params: {
          userAccessKey: context.state.userAccessKey,
        }})
        .then(response => {
          context.commit('updateCartProductsData', response.data.items);
        })
        .catch(() => {
          context.commit('syncartproducts');
        })
    },
    loadOrderInfo(context, orderId) {
      return axios.get(API_BASE_URL + `/api/orders/${orderId}`, {
        params: {
          userAccessKey: context.state.userAccessKey,
        }
      })
        .then(response => context.commit('updateOrderInfo', response.data))

    }
  }
});