<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">
            Каталог
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html">
            Корзина
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>
  
      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        3 товара
      </span>
    </div>
  
    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormTextField v-model="formData.name" title="ФИО" type="text" :error="formError.name"
              placeholder="Введите ваше полное имя" />
            <BaseFormTextField v-model="formData.address" title="Адрес доставки" type="text" :error="formError.address"
              placeholder="Введите ваш адрес" />
            <BaseFormTextField v-model="formData.phone" title="Телефон" type="tel" :error="formError.phone"
              placeholder="Введите ваш телефон" />
            <BaseFormTextField v-model="formData.email" title="Email" type="email" :error="formError.phone"
              placeholder="Введи ваш Email" />
            <BaseFormTextAreaField v-model="formData.comment" title="Комментарий к заказу" :error="formError.comment"
              placeholder="Ваши пожелания" />
          </div>
          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" value="0" checked="">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>
  
            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only"  type="radio" name="pay" value="cash">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>
  
        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order" v-for="item in products" :key="item.product.id">
              <h3>{{ item.product.title }} - {{ item.amount }} шт</h3>
              <b>{{ (item.product.price * item.amount) | numberFormat}} ₽</b>
              <span>Артикул: {{ item.productId }}</span>
            </li>
          </ul>
  
          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: <b>{{ quantity }}</b> товара на сумму <b>{{ (total + 500) | numberFormat}}₽</b></p>
          </div>
  
          <button class="cart__button button button--primery" type="submit">
            Оформить заказ
          </button>
        </div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BaseFormTextField from '@/components/BaseFormTextField';
import BaseFormTextAreaField from '@/components/BaseFormTextAreaField';
import numberFormat from '@/helpers/numberFormat';
import { mapGetters } from 'vuex';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default {
  components: { BaseFormTextField, BaseFormTextAreaField },
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',
    }
  },
  filters: {
    numberFormat
  },
  computed: {
    ...mapGetters({ products: 'cartDetailsProduct', total: 'cartTotalPrice', quantity: 'quantity' }),
  },
  methods: {
    order() {
      this.formError = {};
      this.formErrorMessage = '';
      axios.post(API_BASE_URL + '/api/orders', {...this.formData}, {
        params: {
          userAccessKey: this.$store.state.userAccessKey
        }
      })
      .then(response => {
        this.$store.commit('resetCart');
        this.$store.commit('updateOrderInfo', response.data);
        this.$router.push({name: 'orderInfo', params: {id: response.data.id}});
      })
      .catch(error => {
          this.formError = error.response.data.error.request || {};
          this.formErrorMessage = error.response.data.error.message;
        })
    }
  },

}
</script> 